<template>
    <div>
        <swiper :options="swiperOption">
            <swiper-slide v-for="(page,index) of pages" :key="index">
                <div class="icons">
                    <template v-for="item of page">
                        <div class="icon" :key="item.id">
                            <img class="icon-img" :src="item.imgUrl" alt="">
                            <p  class="title">{{item.title}}</p>
                        </div>
                    </template>
                </div>
            </swiper-slide>
            <!-- <swiper-slide>
                <div class="icons">
                    <div class="icon">
                        <img class="icon-img" src="http://img1.qunarzz.com/piao/fusion/1803/95/f3dd6c383aeb3b02.png" alt="">
                        <p>热门景点</p>
                    </div>
                </div>
            </swiper-slide> -->
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>

    </div>
</template>
<script>

export default {
  name: "HomeIcons",
  props:{
      icons:{
          type:Array
      }
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination"
        },
        /* 是否循环 */
        loop: true
      }
    };
  },
  mounted() {
    
  },
  computed:{
      pages(){
          const pages =[];
        for(var i=0;i<this.icons.length/8;i++){
            pages[i] = this.icons.slice(8*i,8*(i+1));
        }
        return pages;
      }
  }
};
</script>
<style lang="scss" scoped>
.icons {
  width: 100%;
  height: 50vw;
  // background: #333;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 25vw;
  border-bottom: 1px solid #eee;
}
.icons > div.icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.icon-img {
  width: 110px;
  height: 110px;
}
.title{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>
