<template>
    <div class="wrapper" ref="wrapper">
        <div class="content">
            <div class="area">
                <div class="title">当前城市</div>
                <div class="button-list">
                    <div class="button">{{this.currentCity}}</div>
                </div>
            </div>
            <div class="area">
                <div class="title">热门城市</div>
                <div class="button-list">
                    <div class="button" @click="handleCityClick(item.name)" v-for="item of hotCities" :key="item.id">{{item.name}}</div>
                </div>
            </div>
            <div class="area">
                <div v-for="(item,key) of cities" :key="item.id" :ref="key">
                    <div class="title">{{key}}</div>
                    <div class="item-list">
                        <div class="item" 
                        @click="handleCityClick(city.name)"
                        v-for="city of item" :key="city.id">{{city.name}}</div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
import BScroll from "better-scroll";
import {mapState,mapMutations} from "vuex";
export default {
  name: "CityList",
  props: ["cities", "hotCities",'letter'],
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  watch:{
      letter(){
          if(this.letter){
              const element = this.$refs[this.letter][0];
              this.scroll.scrollToElement(element);
          }
      }
  },
  methods:{
      handleCityClick(city){
        //   this.$store.dispatch('changeCity',city);
        // this.$store.commit('changeCity',city)
        this.changeCity(city);
        this.$router.push('/')
      },
      ...mapMutations(['changeCity'])
  },
  computed:{
      ...mapState({
          currentCity:'city'
      })
  }
};
</script>
<style lang="scss" scoped>
@import "style/global.scss";
.title {
  line-height: 60px;
  background: #eee;
  padding-left: 20px;
}
.button-list {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  grid-row-gap: 10px;
}
.button {
  width: 200px;
  line-height: 50px;
  border: 1px solid #ccc;
  text-align: center;
  border-radius: 15px;
}
.item-list {
  padding: 20px;
  .item {
    line-height: 54px;
    padding-left: 20px;
    color: #666;
    border-bottom: 1px solid #eee;
  }
}
.wrapper {
  width: 100%;
  height: 1000px;
  overflow: hidden;
  position: relative;
}
</style>

