<template>
    <ul class="list">
        <li class="item" v-for="(item,index) of letters" @touchstart="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd" ref="item" :key="index" @click="handleClick">{{item}}</li>
    </ul>
</template>
<script>
export default {
  name: "CityWord",
  props: {
    cities: Object
  },
  data() {
    return {
      touchStatus: false
    };
  },
  computed: {
    letters() {
      const letters = [];
      for (let i in this.cities) {
        letters.push(i);
      }
      return letters;
    }
  },
  methods: {
    handleClick(event) {
      this.$emit("change", event.target.innerText);
    },
    handleTouchStart() {
      this.touchStatus = true;
    },
    handleTouchMove(e) {
      if (this.touchStatus) {
        const startY = this.$refs.item[0].offsetTop;
        const touchY = e.touches[0].clientY;
        const index = Math.floor((touchY - startY) / 20);
        if (index >= 0 && index <= this.letters.length) {
          this.$emit("change", this.letters[index]);
        }
      }
    },
    handleTouchEnd() {
      this.touchStatus = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "style/global.scss";
.list {
  padding-right: 10px;
  position: absolute;
  right: 0;
  width: 50px;
  height: 100%;
  top: 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  line-height: 35px;
  color: color(bg);
}
</style>

