<template>
  <div class="container" @click="handleClose">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <!-- slides -->
        <swiper-slide v-for="item of imgs" :key="item.id">
          <img class="swiper-img" :src="item.imgUrl">
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>
<script>
import { local_data } from "@/data/localData.js";
export default {
  props: {
    imgs: {
      default() {
        return local_data.gallary;
      }
    }
  },
  data() {
    return {
      swiperOption: {
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          type: "fraction"
        },
        observer: true,
        observeParents: true
      }
    };
  },
  methods:{
      handleClose(){
          this.$emit("close")
      }
  }
};
</script>

<style lang="scss" scoped>
.container {
  z-index: 99;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.swiper-img {
  width: 100%;
}
//深度作用选择器
//如果你希望 scoped 样式中的一个选择器能够作用得"更深",例如影响子组件，你可以使用 >>> 操作符：
.wrapper >>> .swiper-pagination-bullet-active {
  background: #fff !important;
}
.swiper-pagination {
  position: absolute;
  bottom: -50px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
}
.wrapper >>> .swiper-container {
  overflow: inherit;
}
</style>

