<template>
  <div class="header">
    <div class="header-fixed-back" v-show="isShowNav" :style="opacityStyle">
      <i class="iconfont back" @click="handleBack">&#xe602;</i>
      <p class="title">知音号游船</p>
    </div>
    <div class="header-ab-back" @click="handleBack" v-show="!isShowNav">
      <i class="iconfont back">&#xe602;</i>
    </div>
  </div>
</template>
<script>
export default {
  name: "DetailHeader",
  data() {
    return {
      isShowNav: false,
      opacityStyle: {
        opacity: 0
      }
    };
  },
  methods: {
    handleBack() {
      this.$router.push("/");
    },
    handleScroll() {
      let top = document.documentElement.scrollTop;
      if (top > 0) {
        let opacity = top / 200;
        opacity = opacity > 1 ? 1 : opacity;
        this.opacityStyle = {
          opacity
        };
        this.isShowNav = true;
      } else {
        this.isShowNav = false;
      }
    }
  },
  /* 页面显示的时候执行 */
  mounted() {
    window.addEventListener("scroll",this.handleScroll);
  },
  /* 组件隐藏的时候执行 */
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>
<style lang="scss" scoped>
@import "style/global.scss";
.header-fixed-back {
  width: 100%;
  background: color(bg);
  height: 88px;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.back {
  position: absolute;
  left: 0;
}
.title {
  font-size: 36px;
  color: #fff;
}
.header-ab-back {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  background: rgba(51, 51, 51, 0.664);
  position: absolute;
  left: 20px;
  top: 20px;
  text-align: center;
  line-height: 65px;
}
</style>


